* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(to right, #360033, #0b8793);
    font-family: sans-serif;
}

header h1 {
    color: #fff;
    margin: 5rem 0;
    font-size: 3rem;
}

header,
form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

form {
    position: relative;
}

form .search {
    display: flex;
}

form button,
form input {
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    background-color: #fff;
    outline: none;
}

form button {
    color: #fff;
    background-color: #340436;
    cursor: pointer;
    transition: all 0.3s ease;
}

form button:hover {
    background-color: #fff;
    color: #340436;
}

.select {
    position: relative;
    margin: 1rem;
    overflow: hidden;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    border: none;

    width: 10rem;
    padding: 1rem;
    cursor: pointer;
    color: #340436;
}

.select:after {
    content: "\25BC";
    position: absolute;
    background-color: #340436;
    color: #fff;
    top: 0;
    right: 0;
    padding: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    pointer-events: none;
}

.select:hover:after {
    background-color: #fff;
    color: #340436;
}

.todo-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.todo-list {
    min-width: 30%;
    list-style: none;
}

.todo {
    margin: 0.5rem;
    background-color: #fff;
    color: #000;
    font-size: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .5s all ease;
}

.todo li {
    width: 100%;
    padding: 0 0.5rem;
}

.complete-btn,
.trash-btn {
    background-color: #960303;
    color: #fff;
    border: none;
    padding: 1rem;
    cursor: pointer;
    font-size: 1rem;
}

.complete-btn {
    background-color: #0b8793;
}


.fa-check-circle,
.fa-minus-circle {
    font-size: 1.5rem;
    pointer-events: none;
}

/*! alert css */
.alert-wrapper {
    position: absolute;
    bottom: 10%;
    right: 0;
}

.alert-success,
.alert-warning {
    padding: 1rem;
    color: #fff;
    font-weight: bold;
    width: 20rem;
    
    
}

.alert-warning {
    background-color: #dc3545;
}

.alert-success {
    background-color: #5ebe92;
}


.fall {
    transform: translateY(8rem) rotateZ(360deg);
    opacity: 0;
}

.completed {
    opacity: 0.5;
    text-decoration: line-through;
}
@media(max-width:484px){
header h1{
    font-size: 1.8em;
}
form button,
form input {
    padding: 0.4rem;
    font-size: 1.2rem;
  
}
select {
   

    width: 10rem;
    padding: 0.5rem;
    
}
.select:after {
   
    padding: 0.5rem;
 
}
.alert-success,
.alert-warning {
    padding: 0.5rem;
    color: #fff;
    font-weight: bold;
   width: 288px;
  

}
.complete-btn,
.trash-btn {
 width: 40px;;
    padding: 0.4rem;
    cursor: pointer;
    font-size: 0.5rem;
    
    
}

.todo li {
    width: 403px;
    
   
}
